import mixins from "~/utils/mixins";
import {common} from "~/utils/api/url";
import {MessageError, MessageSuccess} from "~/utils/utils";
import newSettingValidater from "~/utils/validater/newSettingValidater";
import MyNormalDialog from "~/components/utils/dialog/MyNormalDialog";
import newSettingPayValidater from "~/utils/validater/newSettingPayValidater";
export default {
  components: {MyNormalDialog},
  mixins: [mixins,newSettingValidater,newSettingPayValidater],
  data(){
    return {
      subTitle: '',
      visibleConfim: false,
      tableData: [],
      dialogSetting: false,
      dialogLoading: false,
      dialogPay: false,
      searchKey: '',
      yearOptions: [],
      typeOptions: [],
      form: {
        id: '',
        year: '',
        name: '',
        type: '',
        time: [],
        url: ''
      },
      formPay: {
        url: ''
      }
    }
  },
  created() {
    this.initYear();
    this.init();
  },
  methods: {
    initYear(){
      let year = [];
      let params = {
        page: 1,
        num: 9999
      };
      this.$axios.get(common.enroll_page, {params: params}).then(res => {
        if (res.data.data){
          let year = [];
          for (let i = 0; i < res.data.data.list.length; i++){
            year.push({
              label: res.data.data.list[i].year,
              text: res.data.data.list[i].year,
              value: res.data.data.list[i].year,
            });
          }
          this.yearOptions = year;
        }
      });
    },
    init(){
      let params = {
        page: this.page,
        num: this.num,
        year: this.searchKey
      };
      this.$axios.get(common.enroll_page, {params: params}).then(res => {
        if (res.data.data){
          this.tableData = res.data.data.list;
          this.total = res.data.data.totalCount;
          this.num = res.data.data.num;
          this.page = res.data.data.currentPage;
        }
      });
    },
    getPayInfo(){
      let params = {};
      this.$axios.get(common.enroll_pay_link_get, {params: params}).then(res => {
        if (res.data.data){
          this.formPay = {
            url: res.data.data.enrollPayUrl
          };
        }
      });
    },
    sizeChange(event){
      this.page = 1;
      this.num = event;
      this.init();
    },
    currentPage(event){
      this.page = event;
      this.init();
    },
    jumpPage(data){
      this.page = data;
      this.init();
    },
    handleSearchChange(event){
      this.searchKey = event;
      this.page = 1;
      this.init();
    },
    handleFormChange(event, type){
      if (type == 1){
        this.form.year = event;
      }else if (type == 2){
        this.form.type = event;
      }else if (type == 3){
        this.form.time = [this.$moment(event[0]).format("YYYY-MM-DD HH:mm"), this.$moment(event[1]).format("YYYY-MM-DD HH:mm")];
      }
    },
    payInfo(){
      this.getPayInfo();
      this.dialogPay = true;
    },
    addInfo(){
      this.dialogSetting = true;
    },
    editInfo(item){
      this.form = {
        id: item.id,
        name: item.enrollName,
        year: item.year,
        type: '',
        time: [this.$moment(item.beginTime).format("YYYY-MM-DD HH:mm"), this.$moment(item.endTime).format("YYYY-MM-DD HH:mm")],
      };
      this.dialogSetting = true;
    },
    deleteInfo(item){
      this.form.id = item.id;
      this.visibleConfim = true;
    },
    statusInfo(item, status){
      let params = {
        id: item.id
      };
      params = this.$qs.stringify(params);
      this.$axios.post(common.enroll_set_current, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
      });
    },
    closeDialog(event){
      this.form = {
        id: '',
        year: '',
        name: '',
        type: '',
        time: [],
        url: ''
      };
      this.formPay = {
        url: ''
      };
      this.subTitle = "";
      if (this.$refs['form']){
        this.$refs['form'].resetFields();
      }
    },
    handleCancelChange(data) {
      this.visibleConfim = false;
    },
    handleOkChange(data) {
      this.dialogLoading = true;
      let url = "";
      let params = {
        id: this.form.id
      }
      url = common.enroll_del;
      params = this.$qs.stringify(params);
      this.$axios.post(url, params).then(res => {
        if (res.data.code == 200){
          this.init();
          MessageSuccess(res.data.desc);
        }else {
          MessageError(res.data.desc);
        }
        this.visibleConfim = false;
        this.dialogLoading = false;
      });
    },
    cancelDialog(){
      this.dialogSetting = false;
      this.dialogPay = false;
    },
    okDialog(){
      let url = common.enroll_add;
      this.$refs['form'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            year: this.form.year,
            enrollName: this.form.name,
            beginTime: this.form.time[0],
            endTime: this.form.time[1],
            url: this.form.url
          };
          if (this.form.id != ''){
            params['id'] = this.form.id;
            url = common.enroll_edit;
          }
          params = this.$qs.stringify(params);
          this.$axios.post(url, params).then(res => {
            if (res.data.code == 200){
              this.dialogSetting = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    },
    okPayDialog(){
      let url = common.enroll_pay_link_add;
      this.$refs['formPay'].validate((valid) => {
        if (valid) {
          this.dialogLoading = true;
          let params = {
            enrollPayUrl: this.formPay.url
          };
          params = this.$qs.stringify(params);
          this.$axios.post(url, params, {loading: false}).then(res => {
            if (res.data.code == 200){
              this.dialogPay = false;
              this.init();
              MessageSuccess(res.data.desc);
            }else {
              MessageError(res.data.desc);
            }
            this.dialogLoading = false;
          });
        }
      });
    }
  }
}
